/* Элемент с классом projectSlider является СЛАЙДЕРОМ. Этот слайдер листает скрины о конкретном
    проекте, находясь внутри модального окна. Для слайдера projectSlider генерируется класс
    slick-slider (библиотекой slick). */
.slick-slider {
  /* Делаем высоту слайдера slick равной 100% т.к. наши картинки (класс modal-work__photo)
     не имеют высоты (не имеют физических размеров) из-за того, что они спозиционированы
     абсолютно. */
  height: 100%;
  display: flex;
  flex-direction: column;
  /* В слайдере slick-slider переопределяем стили класса slick-dots (для дефолтных точек
    проистывания слайдера).
      */ }
  .slick-slider .slick-list {
    height: 100%; }
    .slick-slider .slick-list .slick-track {
      height: 100%; }
  .slick-slider .slick-dots {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    bottom: 1.467rem;
    left: 0;
    z-index: 2; }
    .slick-slider .slick-dots li {
      margin: 0 0.5rem; }
      .slick-slider .slick-dots li.slick-active button {
        background-color: #fff; }
      .slick-slider .slick-dots li button {
        padding: 0;
        font-size: 0;
        color: transparent;
        width: 11px;
        height: 11px;
        border: 1px solid #fff;
        border-radius: 50%;
        background: transparent;
        cursor: pointer; }
        .slick-slider .slick-dots li button:focus {
          outline: none; }

/* Сначала идут max-width (от самого высокого числа до самого низкого),
а потом идут min-width (от самого низкого числа до самого высокого). */
/* Открытие/закрытие основного навигационного меню при нажатии на кнопку sandwichMenu,
на мобильной версии сайта */
.sandwichMenu {
  width: 30px;
  padding: 10px 0;
  display: none;
  position: absolute;
  top: 24px;
  right: 24px;
  z-index: 1;
  cursor: pointer;
  font-size: 0;
  color: transparent;
  border: 0;
  background: none; }
  .sandwichMenu:focus {
    outline: none; }
  .sandwichMenu__item {
    display: block;
    width: 100%;
    height: 3px;
    background-color: #2F0C49;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    transition: background 0.2s linear; }
    .sandwichMenu__item::before, .sandwichMenu__item::after {
      content: "";
      width: 100%;
      height: 3px;
      position: absolute;
      left: 0;
      background-color: #2F0C49;
      z-index: 1;
      transition: transform 0.2s linear; }
    .sandwichMenu__item::before {
      top: -8px; }
    .sandwichMenu__item::after {
      bottom: -8px; }
  .sandwichMenu.active .sandwichMenu__item {
    background: none; }
    .sandwichMenu.active .sandwichMenu__item::before {
      transform-origin: left top;
      transform: rotate(45deg) translateY(-3px); }
    .sandwichMenu.active .sandwichMenu__item::after {
      transform-origin: left bottom;
      transform: rotate(-45deg) translateY(3px); }

/* Сначала идут max-width (от самого высокого числа до самого низкого),
а потом идут min-width (от самого низкого числа до самого высокого). */
@media (max-width: 767px) {
  .sandwichMenu {
    display: block; } }

